import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, useStyleConfig
} from '@chakra-ui/react';
import PhoneInput, { isPossiblePhoneNumber } from 'react-phone-number-input';
import 'react-phone-number-input/style.css';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { sendCardPay } from '../../../store/middlewares/payment';
import { updateContact } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import AddressSearch from '../../../components/AddressSearch';
import CurrencyInput from '../../../components/CurrencyInput';
import { useDrawer } from '../../../hooks/drawer';

const PaymentSuccess = React.lazy(() => import('../PaymentSuccess/PaymentSuccess'));

const SendCard:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open } = useDrawer();
  const inputStyle:any = useStyleConfig('Input', {
    variant: 'primary'
  });

  const accountDetails = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    name: contactDetails?.name || '',
    email: contactDetails?.email || '',
    phone: contactDetails?.phone || '',
    address: contactDetails?.card?.address || {
      line1: '',
      line2: '',
      city: '',
      state: '',
      country: 'US',
      postalCode: '',
      addressType: 'card',
    }
  });

  const [paymentData, updatePaymentData] = useState({
    amount: '',
    description: '',
    contactId: contactDetails?.id || '',
    accountId: accountDetails?.id || '',
  });

  const isValid = useMemo(() => localContactDetails.name.length !== 0
  && localContactDetails.email.length !== 0
  && isPossiblePhoneNumber(localContactDetails.phone)
  && paymentData.amount.length !== 0
  && parseFloat(paymentData.amount) >= 0.01
  && paymentData.description.length !== 0
  && !!localContactDetails.address
  && !!localContactDetails.address.line1
  && !!localContactDetails.address.city
  && !!localContactDetails.address.state
  && !!localContactDetails.address.country
  && !!localContactDetails.address.postalCode
  && localContactDetails.address.line1.length !== 0
  && localContactDetails.address.city.length !== 0
  && localContactDetails.address.state.length !== 0
  && localContactDetails.address.country.length !== 0
  && localContactDetails.address.postalCode.length !== 0, [localContactDetails, paymentData]);

  const onSubmit = useCallback(() => {
    if (contactDetails?.id && isValid) {
      dispatch(updateContact(contactDetails.id, {
        accountId: contactDetails.accountId,
        name: localContactDetails.name,
        email: localContactDetails.email,
        phone: localContactDetails.phone,
        card: {
          address: {
            ...localContactDetails.address,
            addressType: 'card',
            country: 'US',
          },
        }
      })).then(() => {
        dispatch(sendCardPay({
          ...paymentData,
          type: 'virtual'
        }))
          .then(() => {
            open(PaymentSuccess);
          }).catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [isValid, paymentData, localContactDetails]);

  return (
    <div className="page">
      <MiniBox
        heading={t('payments_send_card.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={onSubmit}>
            {t('payments_send_card.ctaText')}
          </Button>
        )}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.name')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails?.name || ''}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  name: e.target.value
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.email')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.email}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  email: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.phone')}
            </FormLabel>
            <PhoneInput
              data-testid="mobileInput"
              style={inputStyle.field}
              className="country-input"
              international
              limitMaxLength
              value={localContactDetails.phone}
              defaultCountry="US"
              countryCallingCodeEditable={false}
              onChange={(v:any) => {
                updateContactData({
                  ...localContactDetails,
                  phone: v || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.address1')}
            </FormLabel>
            <AddressSearch
              restrictedCountry="US"
              placeholder={t('payments_send_card.address1')}
              googleApiKey={process.env.REACT_APP_GMAP_KEY}
              address={localContactDetails?.address || undefined}
              onChange={(address) => {
                updateContactData({
                  ...localContactDetails,
                  address: {
                    ...localContactDetails.address,
                    ...address,
                  }
                });
              }}
            />
            {/* <Input variant="primary" value="" /> */}
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.address2')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_send_card.address2')}
              value={localContactDetails?.address?.line2}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateContactData({
                  ...localContactDetails,
                  address: {
                    ...localContactDetails.address,
                    line2: e.target.value,
                  }
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.addressCity')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_send_card.addressCity')}
              value={localContactDetails?.address?.city}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateContactData({
                  ...localContactDetails,
                  address: {
                    ...localContactDetails.address,
                    city: e.target.value,
                  }
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.addressState')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_send_card.addressState')}
              value={localContactDetails?.address?.state}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateContactData({
                  ...localContactDetails,
                  address: {
                    ...localContactDetails.address,
                    state: e.target.value,
                  }
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.addressZipcode')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_send_card.addressZipcode')}
              value={localContactDetails?.address?.postalCode}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateContactData({
                  ...localContactDetails,
                  address: {
                    ...localContactDetails.address,
                    postalCode: e.target.value,
                  }
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.amount')}
            </FormLabel>
            <CurrencyInput
              value={paymentData.amount}
              onChange={(value) => {
                updatePaymentData({
                  ...paymentData,
                  amount: value || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_send_card.purpose')}
            </FormLabel>
            <Input
              variant="primary"
              value={paymentData.description}
              onChange={(e) => {
                updatePaymentData({
                  ...paymentData,
                  description: e.target.value
                });
              }}
            />
          </FormControl>

        </Box>
      </MiniBox>
    </div>
  );
};

export default SendCard;
