import http from '../../utils/http';
import { loading } from '../../utils/general';
import { AsyncAppAction } from '..';
import { updatePaymentResponse } from '../actionCreators/payment';
import { paymentResponse } from '../../types/payment';

/**
 * intrabank
 * @param payload
 * @returns
 */
export const intrabankPay = (payload: {
    accountId: string,
    contactId: string,
    amount: string,
    description: string
  }): AsyncAppAction<paymentResponse> => (dispatch) => new Promise((resolve, reject) => {
  /* */
  loading(true);
  http
    .post<paymentResponse>('v1/send/intrabank', payload)
    .then(({ data }) => {
      dispatch(updatePaymentResponse(data));
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

